.LoadingIndicator_Loading_Indicator__3pROL {
    display: inline-block;
    position: relative;
    width: 36px;
    height: 36px;
  }
  .LoadingIndicator_Loading_Indicator__3pROL div {
    display: block;
    position: absolute;
    width: 15px;
    height: 15px;
    margin: 3px;
    border: 6px solid #ff2058;
    border-radius: 50%;
    animation: LoadingIndicator_Loading_Indicator__3pROL 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #ff2058 transparent transparent transparent;
  }
  .LoadingIndicator_Loading_Indicator__3pROL div:nth-child(1) {
    animation-delay: -0.45s;
  }
  .LoadingIndicator_Loading_Indicator__3pROL div:nth-child(2) {
    animation-delay: -0.3s;
  }
  .LoadingIndicator_Loading_Indicator__3pROL div:nth-child(3) {
    animation-delay: -0.15s;
  }
  @keyframes LoadingIndicator_Loading_Indicator__3pROL {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
